.user-slider {
  position: relative;
  width: 100%;
}

.user-slider_list.swiper-wrapper {
  width: 100%;
  height: 887px;
  overflow: hidden;
}

.user-slider__item.swiper-slide {
  display: flex;
  max-width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.user-slider__item--1  {
  background-image: url("../img/slider-bottom_01.jpg");
}

.user-slider__item--2  {
  background-image: url("../img/slider-bottom_02.jpg");
}

.user-slider__item--3  {
  background-image: url("../img/slider-bottom_03.jpg");
}
