.site-link {
  position: relative;
  color: #000000;
  text-decoration: none;
}

.site-link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 4px;
  left: 0;
  bottom: -4px;
  text-decoration: none;
  z-index: -1;
}

.site-link--pink::after {
  background-color: #ef1993;
}

.site-link--orange::after {
  background-color: #f99205;
}

.site-link--green::after {
  background-color: #1dbc08;
}

.site-link--cool-pink::after {
  background-color: #ea46e3;
}

.site-link--blue::after {
  background-color: #4ac9dc;
}
